.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 20;
  /* background-color: rgba(255, 255, 255, 0.75); */
}

.modal {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 90%;
  height: 50vh;
  color: rgb(238, 238, 238);
  background-color: rgb(20, 20, 20);
  padding: 1rem;
  border-radius: 14px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
  z-index: 30;
  animation: slide-down 300ms ease-out forwards;
}
/* .modal > div {
  display: flex;
  flex-flow: row;
  width: 100%;
} */
@media (min-width: 768px) {
  .modal {
    width: 90rem;
    left: calc(24% - 20rem);
  }
}

@keyframes slide-down {
  from {
    opacity: 0;
    transform: translateY(-3rem);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
